em.name}}\n          </router-link> -->\n        </div>\n      </div>\n      <el-table :data=\"list\">\n        <el-table-column label=\"姓名\"\n                         align=\"center\"\n                         prop=\"name\">\n\n        </el-table-column>\n        <el-table-column label=\"手机号\"\n                         prop=\"phone\"\n                         align=\"center\">\n\n        </el-table-column>\n        <el-table-column label=\"市级名称\"\n                         prop=\"city_name\"\n                         align=\"center\">\n\n        </el-table-column>\n        <el-table-column label=\"金额\"\n                         prop=\"money\"\n                         align=\"center\">\n\n        </el-table-column>\n\n        <el-table-column label=\"创建时间\"\n                         prop=\"create_time\"\n                         align=\"center\">\n\n        </el-table-column>\n        <el-table-column label=\"操作\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            <el-button type=\"primary\"\n                       @click=\"visibleDetails=true,details(scope.row.id)\"\n                       size=\"small\">详情</el-button>\n            <el-button type=\"primary\"\n                       size=\"small\"\n                       @click=\"detailsVisible=true,through(scope.row)\"\n                       :loading='throughLoading'>通过</el-button>\n          </template>\n        </el-table-column>\n      </el-table>\n      <el-dialog title=\"详情\"\n                 :visible.sync=\"visibleDetails\"\n                 center>\n        <div class=\"margin\">\n          <span>补交押金 : </span>\n          <span>{{DetailsList.money}}元</span>\n        </div>\n        <div class=\"margin\">\n          <span>支付时间 : </span>\n          <span>{{DetailsList.create_time}}</span>\n        </div>\n        <div>\n          <div v-for='(item,index) in DetailsList.img'\n               :key=\"index\"\n               class=\"images\">\n            <div>\n 