es.data.data.data || []\n        this.pageCount = res.data.data.rows || 0\n      } catch (err) {\n        throw (err)\n      }\n    },\n\n    //通过审核\n    async through (row) {\n      this.$confirm('此操作是通过审核, 是否继续?', '提示', {\n        confirmButtonText: '确定',\n        cancelButtonText: '取消',\n        type: 'warning'\n      }).then(async () => {\n        try {\n          this.throughLoading = true\n          const res = await this.$axios.post('admin/VipManager/auditHandle', { token: this.token, id: row.id, tid: row.tid })\n          this.throughLoading = false\n          if (res.data.code == 1) {\n            this.$message({ message: res.data.msg, type: 'success' })\n            this.init()\n          } else {\n            this.$message.error(res.data.msg)\n          }\n        } catch (err) {\n          throw (err)\n        }\n      }).catch(() => { });\n    },\n    async details (id) {  //查看详情\n      try {\n        const res = await this.$axios.post('admin/VipManager/payListDetail', { id: id, token: this.token })\n        this.DetailsList = res.data.data\n\n        this.$nextTick(() => {\n          Viewer(this.$refs.images)\n        })\n      } catch (err) {\n        throw (err)\n      }\n    },\n    erAuth () {\n      var id = this.$route.query.id;\n      this.curId = id;\n      this.$axios.post('admin/Auth/erAuth', {\n        token: window.sessionStorage.getItem('bbytoken'),\n        id: id\n      }).then(res => {\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n          for (var i = 0; i < arr.length; i++) {\n            if (arr[i].son) {\n              if (arr[i].name == '待审核列表') {\n                this.seCurId = arr[i].id;\n                this.threeAuthList = arr[i].son;\n              }\n\n            }\n          }\n          this.authList = arr;\n        } else {\n          this.$alert(res.data.msg, '提示', {\n            type: 'error'\n          });\n        }\n      })\n        .catch(err => {\n          alert(err);\n        })\n    }\n\n  },\n  mounted () {\n    this.init()\n    this.erAuth()\n  },\n\n\n}\n</script>\n<style scoped>\n.details > div {\n  padding: 5px 0;\n}\n.details > div > span:first-child {\n  display: inline-block;\n  width: 95px;\n  text-align: right;\n  margin-right: 10px;\n}\n.details > div > span > img {\n  width: 50px;\n  height: 50px;\n  margin-right: 10px;\n  vertical-align: middle;\n}\n.margin {\n  margin: 10px 0;\n}\n.images > div {\n  display: inline-block;\n  width: 90px;\n  height: 90px;\n  margin: 0 10px 30px 0;\n  text-align: center;\n}\n.images > div > span {\n  display: inline-block;\n  width: 100%;\n  height: 100%;\n  background-color: #ccc;\n}\n.images img {\n  width: 100%;\n  height: 100%;\n}\n</style>"],"sourceRoot":""}]);

